.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    position: absolute;
    transition: top 3s ease-in-out, left 3s ease-in-out, height 3s ease-in-out;
    top: 50px;
    z-index: 2;
  }
  .App-logo.App-back {
    animation: App-logo-spin infinite 3s alternate;
    position: absolute;
    z-index: 1;
    opacity: 0.3;
  }
}

.content, .no-content {
  background-color: hsl(219, 15%, 47%);
  height: calc(100vh - 120px);
  padding: 10px;
  display: flex;
}

.no-content {
  align-items: center;
  justify-content: center;
  font-size: large;
  color: white;
}

.contactCard {
  background-color: #282c347f;
  border-radius: 8px;
  margin: 10px;
  width: 200px;
  height: 260px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}
.contactCardHeader {
  height: 40px;
  padding: 10px;
  color: white;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.contactCardHeader > div {
  padding-left: 10px;
  padding-right: 10px;
}

.contactCardContent {
  background-color: #f4f4f4;
  width: 100%;
  height: 100%;
  padding-top: 10px;
  border-radius: 0 0 8px 8px;
  overflow-y: scroll;
}

.App-header {
  background-color: #282c34;
  transition: min-height 3s ease-in-out;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header.load {
  justify-content: center;
}

.addButton {
  background: #4456A5;
  transition: background-color 0.3s ease-in-out;
  margin: 10px;
  padding-top: 10px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  cursor: pointer;
}

.button {
  margin: 10px;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  border: #282c3400 1px solid;
  animation: border-color 0.3s ease-in-out;
}

.button:hover {
  border: #282c34 1px solid;
}

.button.main {
  background: #4456A5;
  color: white;
  transition: background-color 0.3s ease-in-out;
}

.addButton:hover, .button.main:hover {
  background: #7FBF6A;
}

.App-header.ready {
  min-height: 120px;
}

.App-header.ready .App-logo {
  height: 96px;
  left: 10px;
  top: 10px;
}

.App-link {
  color: #61dafb;
}

.dialogField {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.dialogField label {
  margin-bottom: 5px;
}

.dialogField textarea {
  height: 100px;
}

.dialogField input,  .dialogField textarea {
  padding: 5px;
  font-size: medium;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border-radius: 5px;
  border: #282c347f 1px solid;
  animation: border-color 0.3s ease-in-out;
}

.dialog {
  min-height: 100vh;
  min-width: 100vw;
  z-index: 100;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.dialog > div {
  background: white;
  border-radius: 10px;
}

.dialogTitle {
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 10px;
  background-color: #282c34;
  padding: 20px;
  border-radius: 10px 10px 0 0;
  color: white;
  min-width: 300px;
}

.dialogContent {
  padding: 20px;
  min-width: 300px;
  color: black;
}

.dialogActions {
  padding: 20px;
  display: flex;
  justify-content: flex-end;
  min-width: 300px;
  border-radius: 0 0 10px 10px;
}

@keyframes App-logo-spin {
  from {
    transform: scale(1.3);
  }
  to {
    transform: scale(1.05);
  }
}

.contactCardPhotos {
  margin-top: 10px;
  border-top:rgba(0, 0, 0, 0.5) 1px solid;
  padding-top: 10px;
}


.pictureGrid {
  display: flex;
  flex-wrap: wrap;
}

.pictureGrid > div {
  flex-basis: 50%;
  flex-grow: 1;
  display: flex;

  min-height: 40px;
  align-items: center;
  justify-content: center;
}

.pictureGrid img {
  width: 100%;
  object-fit: cover;
}
